@import '../../styles/global';
.container {
    padding: 50px 0;
    width: 100%;
    background: white;
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.1);
    z-index: 10;
    
    position: fixed;
    top: 0;
    transition: all .3s ease-in;

    &.active {
        padding: 20px;
        background: $textColor;
        .nav {
            
            a {
                color: white;
            }
        }
    }

    
}